.interface-interface-skeleton__content {
  transition: opacity 150ms cubic-bezier(0.77, 0, 0.175, 1);
}
.interface-interface-skeleton__content.site-design-updating {
  opacity: 0.33;
  pointer-events: none;
  transition: opacity 300ms cubic-bezier(0.77, 0, 0.175, 1);
}

.interface-complementary-area__pin-unpin-item {
  display: none !important;
}

.interface-complementary-area-header .components-button.has-icon {
  padding: 6px 13px;
}

.components-site-design-styles {
  padding: 16px;
}
.components-site-design-styles .components-radio-control .components-base-control__label {
  margin-bottom: 15px;
}
.components-site-design-styles .components-radio-control__option:not(:last-child) {
  margin-bottom: 7px;
}
.components-site-design-styles label span {
  display: inline-flex;
}
.components-site-design-styles .components-spinner {
  margin: 0 0 0 10px;
}

.components-site-design__custom {
  background-color: #f0f0f0;
  border: 1px solid var(--wp-admin-theme-color);
  border-radius: 4px;
  margin-bottom: 2em;
  padding: 1em 16px 4px;
  position: relative;
}
.components-site-design__custom__dismiss {
  background: none;
  border: 0;
  display: block;
  height: 20px;
  padding: 0;
  position: absolute;
  right: 1em;
  top: calc(1em - 1px);
  width: 16px;
}
.components-site-design__custom__dismiss:hover {
  cursor: pointer;
}
.components-site-design__custom__dismiss::after, .components-site-design__custom__dismiss::before {
  background-color: #1e1e1e;
  content: "";
  display: block;
  height: 2px;
  position: relative;
  width: 16px;
}
.components-site-design__custom__dismiss::before {
  top: 2px;
  transform: rotate(-45deg);
}
.components-site-design__custom__dismiss::after {
  transform: rotate(45deg);
}
.components-site-design__custom__dismiss > span {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.components-site-design-designs__design {
  background-color: transparent;
  border: 1px solid #f0f0f0;
  border-radius: 4px;
  display: block;
  margin-bottom: 0.5em;
  padding: 1.25em;
  width: 100%;
}
.components-site-design-designs__design:hover {
  cursor: pointer;
}
.components-site-design-designs__design:focus {
  outline-color: var(--wp-admin-theme-color);
}
.components-site-design-designs__design--current {
  border-color: #1e1e1e;
  margin-bottom: 0;
}
.components-site-design-designs__design--selected {
  border-color: #1e1e1e;
}
.components-site-design-designs__design--updating {
  opacity: 0.5;
  pointer-events: none;
}
.components-site-design-designs__design--updating:hover {
  cursor: default;
}
.components-site-design-designs__design__content {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.components-site-design-designs__design__label {
  font-size: 1.2rem;
}
.components-site-design-designs__design__colors {
  display: flex;
  flex-direction: row-reverse;
  margin: 0;
  padding: 0;
}
.components-site-design-designs__design__color {
  border: 2px solid #fff;
  border-radius: 24px;
  display: inline-block;
  height: 24px;
  margin: 0 0 0 -11px;
  width: 24px;
}

.components-site-design-color-palettes .color-palettes {
  padding-bottom: 1em;
}
.components-site-design-color-palettes .color-palettes__title {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0.5em;
}
.components-site-design-color-palettes .color-palettes__title > button {
  background: none;
  border: 0;
  color: var(--wp-admin-theme-color);
  margin: 0;
  padding: 0;
}
.components-site-design-color-palettes .color-palettes__title > button:hover {
  cursor: pointer;
}
.components-site-design-color-palettes .color-palettes__palettes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.components-site-design-color-palettes .color-palette {
  border: 1px solid #f0f0f0;
  border-radius: 4px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  margin: 0.5em 0;
  padding: 2em;
  width: calc(50% - 0.5em);
}
.components-site-design-color-palettes .color-palette.is-selected {
  border-color: #1e1e1e;
}
.components-site-design-color-palettes .color-palette .component-color-indicator {
  border: 2px solid #fff;
  border-radius: 24px;
  display: inline-block;
  height: 24px;
  margin: 0 0 0 -11px;
  width: 24px;
}
.components-site-design-color-palettes .color-palette .component-color-indicator:last-child {
  margin-left: 0;
}
.components-site-design-color-palettes .color-palette__preview-button.components-button {
  display: block;
  height: 38px;
  padding: 0;
  position: relative;
  width: 100%;
}
.components-site-design-color-palettes .color-palette__preview-button.components-button::before {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}
.components-site-design-color-palettes .color-palette-custom__title {
  margin-bottom: 1em;
}
.components-site-design-color-palettes .color-palette-custom .components-dropdown {
  margin: 10px 0 2px;
  width: 100%;
}
.components-site-design-color-palettes .color-palette-custom .components-dropdown:last-child {
  margin-bottom: 12px;
}
.components-site-design-color-palettes .color-palette-custom .components-button {
  color: #1e1e1e !important;
  font-size: 13px;
  font-weight: 400;
  text-decoration: none;
}
.components-site-design-color-palettes .color-palette-custom .component-color-indicator {
  border: none;
  border-radius: 100%;
  height: 22px;
  margin: 3px 14px 3px 3px;
  position: relative;
  width: 22px;
}
.components-site-design-color-palettes .color-palette-custom .component-color-indicator::before {
  border: 2px solid #1e1e1e;
  border-radius: 100%;
  bottom: -3px;
  content: "";
  left: -3px;
  position: absolute;
  right: -3px;
  top: -3px;
}

.components-nextgen-site-design.save-changes {
  padding: 0;
  position: absolute;
  right: 19px;
  top: -42px;
}
.components-nextgen-site-design.save-changes .components-button.is-primary {
  border-radius: 2px;
}
.components-nextgen-site-design.save-changes .components-button.is-primary:disabled {
  background: var(--wp-admin-theme-color);
  border-color: var(--wp-admin-theme-color);
  color: rgba(255, 255, 255, 0.4);
}
.components-nextgen-site-design.save-changes .components-base-control__field {
  margin-bottom: 0;
}

.components-site-design-fonts button {
  outline: none;
}
.components-site-design-fonts button:hover {
  cursor: pointer;
}
.components-site-design-fonts__heading {
  margin-bottom: 10px;
}
.components-site-design-fonts__heading > p {
  color: #757575;
}
.components-site-design-fonts__heading > p,
.components-site-design-fonts__heading > button {
  display: inline-block;
  margin: 0;
}
.components-site-design-fonts__heading__custom {
  background: none;
  border: none;
  color: var(--wp-admin-theme-color);
  float: right;
  padding: 0;
}
.components-site-design-fonts .components-site-design__custom {
  padding-bottom: 16px;
}
.components-site-design-fonts .components-site-design__custom hr {
  border-bottom-color: #757575;
}
.components-site-design-fonts .components-site-design__custom__body {
  margin-top: 20px;
}
.components-site-design-fonts .components-site-design__custom .components-custom-select-control,
.components-site-design-fonts .components-site-design__custom .components-range-control .components-base-control__field {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.components-site-design-fonts .components-site-design__custom .components-custom-select-control:last-child,
.components-site-design-fonts .components-site-design__custom .components-range-control .components-base-control__field:last-child {
  margin-bottom: 0;
}
.components-site-design-fonts .components-site-design__custom .components-custom-select-control__label,
.components-site-design-fonts .components-site-design__custom .components-custom-select-control .components-base-control__label,
.components-site-design-fonts .components-site-design__custom .components-range-control .components-base-control__field__label,
.components-site-design-fonts .components-site-design__custom .components-range-control .components-base-control__field .components-base-control__label {
  display: inline-block;
  margin-bottom: 0;
}
.components-site-design-fonts .components-site-design__custom .components-custom-select-control__button,
.components-site-design-fonts .components-site-design__custom .components-range-control .components-base-control__field__button {
  background-color: #fff;
  float: right;
}
.components-site-design-fonts .components-site-design__custom .components-custom-select-control__item,
.components-site-design-fonts .components-site-design__custom .components-range-control .components-base-control__field__item {
  color: #1e1e1e;
}
.components-site-design-fonts .components-site-design__custom .components-custom-select-control .components-range-control__root,
.components-site-design-fonts .components-site-design__custom .components-range-control .components-base-control__field .components-range-control__root {
  padding-right: 10px;
  width: 60%;
}
.components-site-design-fonts__options {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.components-site-design-fonts__option {
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  margin-bottom: 1em;
}
.components-site-design-fonts__option__section {
  background: none;
  border: none;
  padding: 1em 1.5em 1.5em;
  text-align: left;
}
.components-site-design-fonts__option__primary {
  font-size: 1.5rem;
  margin-bottom: 0.15em;
}
.components-site-design-fonts__option__secondary {
  font-size: 0.8rem;
  margin-bottom: 0;
  max-width: 88%;
}
.components-site-design-fonts__option__edit {
  background-color: #fff;
  border: none;
  color: var(--wp-admin-theme-color);
  display: inline-block;
  font-size: 0.9rem;
  margin-bottom: 0;
  margin-left: -10px;
  padding: 10px 10px 0;
}
.components-site-design-fonts__option__edit > .icon {
  position: relative;
  top: 6px;
}
.components-site-design-fonts__option__settings {
  border-top: 1px solid #1e1e1e;
  margin-top: -30px;
  padding-top: 30px;
}
.components-site-design-fonts__option__settings .components-font-size-picker__number-container input {
  min-height: auto;
  padding: 5px;
  width: 68px;
}
.components-site-design-fonts__option:hover {
  border-color: var(--wp-admin-theme-color);
}
.components-site-design-fonts__option.is-selected {
  border-color: #1e1e1e;
}
.components-site-design-fonts__option .components-font-size-picker .components-font-size-picker__controls {
  align-items: flex-end;
  flex-wrap: nowrap;
}
.is-heebo .components-site-design-fonts__option__primary {
  font-weight: 600;
}
.is-heebo .components-site-design-fonts__option__secondary {
  font-size: 0.75rem;
}
.is-work .components-site-design-fonts__option__primary, .is-trocchi .components-site-design-fonts__option__primary {
  font-size: 1.4rem;
}